.aboutContainer {
    width: 100%;
 
    background-color: antiquewhite;

    .about-cover {
        position: relative;
        width: 100%;
        height: 275px;
        //  background-image: url("../../images/Coding-workshop.png");
        background-position: center;
        background-size: 100%;
        background-repeat: no-repeat;
        margin-bottom: 20px;
    }
    .overlay {
        position: absolute;
        width: 100%;
        top: 75px;
        height: 300px;
        height: 300px;
        background-image: linear-gradient(180deg, rgba(59, 202, 164, 0.337), rgb(1, 21, 18));

        .aboutTilte {
            padding-top: 120px;
            text-align: center;
            font-size: 4rem;
            font-weight: 600;
            font-family: serif;
            color: white;
        }
    }
}

.education-section {
    position: relative;
}
.category-container {
    margin: 60px 0;

    top: 50px;
    .category {
        width: 90%;
        margin: 25px auto 0 auto;
        background-color: rgb(4, 85, 61);
        box-shadow: 0px 3px 1px gray;

        ul {
            display: flex;
            margin-left: 0px;
            li {
                list-style: none;
                padding: 8px 8px;
                margin: 1px 5px 1px 1px;
                font-size: 1.2rem;
                cursor: pointer;
                color: white;
                font-family: Verdana, Geneva, Tahoma, sans-serif;
            }
            // li:hover {
            //     background-color: azure;
            //     color: rgb(12, 12, 12);
            // }
            .active {
                background: white;
                color: black;
            }
        }
    }
}

